/*------------------------------------*\
Profile
\------------------------------------*/
.Profile__container {
  padding: 10rem 5rem;
  transform: skew(0deg, -4deg);
  background: #dde4e8;
  color: #333;
  margin-bottom: 12rem; }
  @media (max-width: 780px) {
    .Profile__container {
      padding: 10rem 0; } }

.Profile__wrapper {
  display: flex;
  align-items: flex-start; }
  @media (max-width: 780px) {
    .Profile__wrapper {
      flex-wrap: wrap; } }

.Profile__body {
  padding: 4rem; }
  @media (max-width: 780px) {
    .Profile__body {
      padding: 0; } }

.Profile__img {
  filter: drop-shadow(-11px -2px 26px rgba(0, 0, 0, 0.14));
  text-align: center; }
  @media (max-width: 780px) {
    .Profile__img {
      width: 100%;
      margin-bottom: 4rem; } }
